@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: #f1c17d;
  font-family: "Open Sans", sans-serif;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  /* padding-bottom: 60px; */
  display: flex;
  flex-direction: column;
}
.notes-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 15px;
}

.t1-main {
  font-size: 20px;
  font-weight: bolder;
  margin-bottom: 5em;
  margin-top: 5em;
}

.App .add-button {
  position: fixed;
  bottom: 45px;
  right: 45px;
  border-radius: 50%;
  font-size: 32px;
  width: 50px;
  height: 50px;
  z-index: 3;
  padding: 0;
}

.note {
  background-color: #feff9c;
  text-decoration: none;
  color: #000;
  margin: 1em;
  transition: all ease 0.3s;
  height: 10em;
  width: 10em;
  display: flex;

  flex-direction: column;
  -webkit-transform: rotate(-6deg);
          transform: rotate(-6deg);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px,
    rgba(0, 0, 0, 0.1) 0px 2px 4px 0px,
    rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
}

.note:nth-child(even) {
  -webkit-transform: rotate(4deg);
          transform: rotate(4deg);
  position: relative;
  top: 5px;
  background-color: #7afcff;
}

.note:nth-child(3n) {
  -webkit-transform: rotate(-3deg);
          transform: rotate(-3deg);
  position: relative;
  top: -5px;
  background-color: #ff7eb9;
}
*:focus {
  outline: 0;
}
.note__text {
  flex: 1 1;
  padding-right: 0.5em;
  padding-left: 1em;
  margin-top: 1.4em;
  margin-bottom: 0.9em;
  margin-right: 9px;
  overflow: auto;
}

.note__text::-webkit-scrollbar {
  width: 4px;
}
.note__text::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}
.note__text::-webkit-scrollbar-thumb {
  border-radius: 5px;
  box-shadow: inset 0 0 6px grey;
}

.note:hover,
.note.note--focused {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  z-index: 5;
  border-radius: 1em;
}
.note__link {
  text-align: left;
  position: absolute;
  left: 5px;
  top: 2px;
  font-size: 15px;
}
.note__link a,
.note__link a:hover {
  color: blue;
}

.note__link a {
  opacity: 0.6;
}
.note__link a:hover {
  opacity: 1;
}

